<template>
    <div class="list_box">
        <div class="searchBox">
            <h3><span @click='goLast' class="icon-left"></span>
                <p>我的钱包</p>
            </h3>
        </div>
        <div class="getMoney">
            <p class="top">提现金额</p>
            <div class="userMoney">
                <span>&yen;</span><input v-model="userMoney" />
            </div>
            <p class="under">本次可提现金额 &yen; {{money|tofixTwo}}</p>
        </div>

        <div class="wallet_btn">
            <yd-button @click.native="getMoneys()" bgcolor="#5e1515" color="#fff" size="large" type="danger">提&#12288;现</yd-button>
        </div>

    </div>
</template>
<script>
    import ajax from "../../ajax/ajax.js";
    import dataURL from "../../server/dataUrl.js";
    export default {
        data() {
            return {
                userMoney: 0,
                money: 0
            }
        },
        methods: {
            IWS_CheckDecimal(obj) {
                var temp = /^\d+\.?\d{0,2}$/;
                if (temp.test(obj.value)) {
                } else {
                    obj.value = obj.value.substr(0, obj.value.length - 1);
                    IWS_CheckDecimal(obj);
                    // return false;
                }
            },
            async getMoneys() {
                let temp = /^(([1-9]\d*)(\.\d{1,2})?)$|^(0\.0?([1-9]\d?))$/;
                if (!temp.test(this.userMoney)) {
                    this.$dialog.toast({
                        mes: "请输入正确的提现金额",
                        timeout: 1500,
                        icon: "error"
                    });
                    return
                }
                if (this.userMoney%1!=0) {
                    this.$dialog.toast({
                        mes: "提现金额必须为整数",
                        timeout: 1500,
                        icon: "error"
                    });
                    return
                }
                let res = await ajax.Post(dataURL.main.withdraw, {
                    amount: this.userMoney
                });
                this.$dialog.toast({
                    mes: "提现成功",
                    timeout: 1500,
                    icon: "success"
                });
            },
            goLast() {
                this.$router.go(-1);
            },
            linkTo(adr) {
                this.$router.push({
                    path: adr
                });
            },
            async getUserinfo() {
                let res = await ajax.Get(dataURL.main.userInfo);
                console.log(res);
                this.money =  res.wallte.balance ;
            }
        },
        filters: {
            tofixTwo: function (value) {
                return (parseFloat(value) / 10000).toFixed(2);
            }
        },
        created() {
            this.getUserinfo();
        }
    }
</script>


